
.progress-list {
  padding: 12px 20px;
  -webkit-align-items: center;
      -ms-flex-align: center;
          align-items: center;
}
.progress-list h3 {
    font-weight: 400;
}
.progress-list .win {
    color: red;
}
.progress-list .lose {
    color: green;
}
.progress-list .desc {
    color: #999;
    margin-bottom: 0;
    margin-top: 5px;
    font-size: 12px;
}



