
.forgot-bg {
  background: url(../assets/stardust1.png) repeat;
  position: absolute;
  height: 100%;
  top: 0;
  width: 100%;
}
.forgot-container {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
}
.forgot-tab__title {
  height: 1.2rem;
  line-height: 1.2rem;
  text-align: center;
  font-size: 16px;
  background: rgba(0, 0, 0, 0.4);
  color: #ddd;
}
.forgot-tab__title > div {
    border-bottom: 2px solid transparent;
}
.forgot-tab__title .active {
    color: #fff;
    border-bottom: 2px solid rgba(255, 255, 255, 0.5);
    background: rgba(0, 0, 0, 0.8);
}
.forgot-tab {
  background: rgba(0, 0, 0, 0.7);
  position: absolute;
  width: 100%;
  bottom: 0;
  top: 6rem;
}
.forgot-tab__box {
  padding: 0.8rem 50px 0;
}
.form-email {
  background: rgba(0, 0, 0, 0.7);
  border-radius: 3px;
  height: 40px;
  line-height: 40px;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
}
.form-email i {
    font-size: 20px;
    color: #ddd;
    margin: 0 5px;
}
.form-email input {
    display: block;
    -webkit-flex: 1;
        -ms-flex: 1;
            flex: 1;
    background: transparent;
    -webkit-appearance: none;
       -moz-appearance: none;
            appearance: none;
    border: none;
    color: #fff;
}
.form-email input[disabled] {
      color: #868080;
}
.form-continue {
  margin-top: 50px;
}
.form-btn {
  background-color: rgba(73, 195, 6, 0.7);
  text-align: center;
  font-size: 16px;
  border-radius: 2px;
  padding: 10px 0;
  color: #fff;
}
.forgot-back {
  position: absolute;
  width: 50px;
  height: 50px;
  top: 10px;
  line-height: 50px;
  text-align: center;
  left: 10px;
}
.forgot-back i {
    font-size: 30px;
    color: #fff;
    font-weight: bold;
}



