
@charset "UTF-8";
.mod-user {
  color: #999;
}
.mod-user .ta-r {
    color: #494949;
    padding-right: 30px;
    text-align: right;
}
.dot {
  display: inline-block;
  height: 1em;
  line-height: 1;
  text-align: left;
  vertical-align: -.25em;
  overflow: hidden;
}
.dot::before {
  display: block;
  content: '...\A..\A.';
  white-space: pre-wrap;
  /* 也可以是white-space: pre */
  -webkit-animation: dot 3s infinite step-start both;
          animation: dot 3s infinite step-start both;
}
@-webkit-keyframes dot {
33% {
    -webkit-transform: translateY(-2em);
            transform: translateY(-2em);
}
66% {
    -webkit-transform: translateY(-1em);
            transform: translateY(-1em);
}
}
@keyframes dot {
33% {
    -webkit-transform: translateY(-2em);
            transform: translateY(-2em);
}
66% {
    -webkit-transform: translateY(-1em);
            transform: translateY(-1em);
}
}
.iconfont.male {
  color: #099AE0;
}
.iconfont.female {
  color: #FE579F;
}
.ev-model {
  position: fixed;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  z-index: 1001;
}
.ev-mask {
  position: fixed;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  background-color: rgba(0, 0, 0, 0.7);
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-justify-content: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-align-items: center;
      -ms-flex-align: center;
          align-items: center;
}
.ev-model-box {
  width: 80%;
  background-color: #fff;
  padding: 20px 0;
  border-radius: 2px;
  text-align: center;
  z-index: 11;
  position: relative;
}
.inner-icon {
  width: 50px;
  height: 50px;
  border: 2px solid currentColor;
  background: #fff;
  border-radius: 100%;
  text-align: center;
  line-height: 46px;
  margin: 0 auto;
}
.inner-icon i {
    font-size: 30px;
    color: currentColor;
}
.select_sex .flex {
  color: #ddd;
}
.select_sex .flex:first-child.active {
  color: #099AE0;
}
.select_sex .flex:last-child.active {
  color: #FE579F;
}



