
.login-container {
  overflow: hidden;
  height: 100%;
  background: url("../assets/reg-bg.jpg") no-repeat center top;
  background-size: cover;
  position: relative;
}
.brand {
  margin: 50px 0 1.33333rem;
  color: #fff;
  text-align: center;
}
.brand h1 {
    font-size: 60px;
    margin-bottom: 20px;
}
.brand h1 span {
      -webkit-transform: scaleX(-1);
          -ms-transform: scaleX(-1);
              transform: scaleX(-1);
      display: inline-block;
}
.brand h2 {
    font-size: 14px;
    font-weight: normal;
    text-transform: capitalize;
    display: inline-block;
    padding: 5px 5px 3px;
    border: 1px solid #ddd;
    border-radius: 2px;
}
@media screen and (-webkit-min-device-pixel-ratio: 2) {
.brand h2 {
        position: relative;
        border: 0;
}
.brand h2:before {
          content: "";
          width: 200%;
          height: 200%;
          position: absolute;
          top: 0;
          left: 0;
          border: 1px solid #ddd;
          -webkit-transform: scale(0.5);
              -ms-transform: scale(0.5);
                  transform: scale(0.5);
          -webkit-transform-origin: 0 0;
              -ms-transform-origin: 0 0;
                  transform-origin: 0 0;
          padding: 1px;
          border-radius: 4px;
          pointer-events: none;
}
}
.login-form {
  background: rgba(0, 0, 0, 0.7);
  padding: 0 20px;
}
.login-form .label {
    line-height: 30px;
    margin: 15px 5px;
}
.login-form .label i {
      font-size: 20px;
}
.login-form input {
    width: 100%;
    margin: 15px 0;
    line-height: 30px;
    height: 30px;
    border: none;
    background: none;
    -webkit-appearance: none;
       -moz-appearance: none;
            appearance: none;
    color: #ddd;
    font-size: 14px;
}
.login-form input:focus {
      outline: none;
}
.login-form input:-webkit-autofill {
    background-color: transparent;
}
.login-form__username {
  border-bottom: 1px solid #aaa;
}
@media screen and (-webkit-min-device-pixel-ratio: 2) {
.login-form__username {
      border-bottom: 0;
      background-repeat: repeat-x;
      background-size: 100% 1px;
      background-position: left bottom;
      background-image: -webkit-gradient(linear, left top, left bottom, color-stop(0.5, transparent), color-stop(0.5, #aaa), to(#aaa));
}
}
.login-form__forgot {
  margin-top: 20px;
  padding: 0 20px;
}
.login-form__forgot a {
    color: #ded8d8;
    line-height: 26px;
    height: 26px;
    display: inline-block;
}
.login-btn {
  margin: 20px 30px;
  padding: 10px 0;
  background: rgba(73, 195, 6, 0.7);
  text-align: center;
  font-size: 1.4em;
  color: #fff;
  border: 1px solid #ddd;
  border-radius: 2px;
}
@media screen and (-webkit-min-device-pixel-ratio: 2) {
.login-btn {
      position: relative;
      border: 0;
}
.login-btn:before {
        content: "";
        width: 200%;
        height: 200%;
        position: absolute;
        top: 0;
        left: 0;
        border: 1px solid #ddd;
        -webkit-transform: scale(0.5);
            -ms-transform: scale(0.5);
                transform: scale(0.5);
        -webkit-transform-origin: 0 0;
            -ms-transform-origin: 0 0;
                transform-origin: 0 0;
        padding: 1px;
        border-radius: 4px;
        pointer-events: none;
}
}
.login-btn.reg-btn {
    background: rgba(218, 22, 5, 0.7);
}
.loagined {
  text-align: center;
  color: #fff;
  padding-top: 30px;
}



