
.user-container {
  background-color: #f7f7f7;
  height: 100%;
  position: relative;
  padding-bottom: 1.33333rem;
}
.user-bar {
  background: url(../../assets/stardust1.png) repeat;
  color: #fff;
  padding: 0.8rem 0.4rem;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-align-items: center;
      -ms-flex-align: center;
          align-items: center;
}
.user-bar .flex {
    padding-left: 20px;
}
.avatar {
  width: 2.4rem;
  height: 2.4rem;
  position: relative;
  margin: 0 auto;
}
.avatar span {
    height: 2.4rem;
    border: 2px solid #fff;
    display: block;
    background-color: #fff;
    border-radius: 100%;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center center;
}
.avatar .update_face {
    position: absolute;
    bottom: 0%;
    right: 0;
    border: 2px solid rgba(0, 0, 0, 0.4);
    width: 26px;
    height: 26px;
    background-color: #fff;
    border-radius: 100%;
    color: #54c117;
    text-align: center;
    line-height: 22px;
}
.avatar input {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background: transparent;
    opacity: 0;
}
.ta-c {
  text-align: center;
}
.avatar__name {
  font-size: 16px;
}
.avatar__email {
  font-size: 12px;
  color: gray;
  margin-top: 5px;
  margin-bottom: 0;
}
.section {
  background-color: #fff;
  margin: 0.4rem 0;
}
.item {
  font-size: 16px;
}
.item .item-list {
    padding-left: 0.4rem;
    position: relative;
}
.item .item-list > div {
      padding: 0.34667rem 0 0.34667rem 0;
}
.item .item-list:active {
      background-color: #f2f2f2;
}
.item .item-list.no-active:active {
      background-color: transparent;
}
.item .item-list i.iconfont {
      margin-right: 5px;
      font-size: 20px;
}
.item .item-list.arrow-right:after {
      content: '';
      position: absolute;
      width: 10px;
      height: 10px;
      border-top: 1px solid #ddd;
      border-right: 1px solid #ddd;
      right: 20px;
      -webkit-transform: rotate(45deg);
          -ms-transform: rotate(45deg);
              transform: rotate(45deg);
      transform-orign: center center;
      top: 50%;
      margin-top: -5px;
}
.item.first .item-list:first-child i {
  color: red;
}
.item.first .item-list:nth-child(2) i {
  color: #d200ff;
}
.item.first .item-list:nth-child(3) i {
  color: #1066dc;
}
.item.first .item-list:nth-child(4) i {
  color: #1bca01;
}
.page-placeholder {
  padding-top: 30px;
  text-align: center;
}
.white-bg {
  background-color: #fff;
}
.item-right {
  padding-right: 0.4rem;
  float: right;
}
.bars.child-view {
  top: 4rem;
  overflow: auto;
  -webkit-overflow-scrolling: touch;
  z-index: 1;
  bottom: 1.33333rem;
}



